<script>
    export default {
        name: '',
        props: {
            left: {
                type: Number,
                default: 8
            },
            right: {
                type: Number,
                default: 4
            }
        },
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


